:root {
  --clr-primary: #1e42a1;
  --clr-white: #fff;
  --clr-dark: #000;
  --clr-svetro-light: #f4f4f4;
  --clr-svetro-green: #72b39c;
  --clr-svetro-red: #ff725e;
  --clr-light-blue: #7879f1;
  --clr-light-gray: #eaeaea;
  --clr-dark-gray: #333;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  line-height: 1.5;
  font-family: 'Inter', sans-serif;
}
img {
  display: block;
  max-width: 100%;
  box-sizing: content-box;
}
h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

input,
textarea,
button {
  font-family: inherit;
}

section {
  padding: 2rem 0;
}

.container {
  width: 90%;
  margin-inline: auto;
}

/* slider */
.slider {
  overflow: hidden;
  position: relative;
}

.innerSlider {
  display: flex;
  gap: 1rem;
  animation: slider 10s linear infinite;
}

/* icons */
.icon {
  font-size: 20px;
}

.btn {
  font-size: 0.875rem;
  background: 0;
  border: 1px solid;
  padding: 0.938rem 1.875rem;
  cursor: pointer;
  transition: all 300ms ease-in;
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  display: block;
  border: 0;
}
.btn__primary {
  color: var(--clr-white);
  background: var(--clr-primary);
  border-color: var(--clr-primary);
  border: 1px solid var(--clr-primary);
}

.btn__primary:hover {
  color: var(--clr-primary);
  background: var(--clr-white);
}

.btn__green {
  background: var(--clr-svetro-green);
  border: 1px solid var(--clr-svetro-green);
  color: var(--clr-white);
}

.btn__green:hover {
  background: var(--clr-white);
  color: var(--clr-svetro-green);
}

.btn__outline {
  background: var(--clr-white);
  border: 1px solid var(--clr-primary);
  color: var(--clr-primary);
}

.btn__outline:hover {
  opacity: 0.8;
}

/* accordion */
.accordion {
  list-style: none;
  max-width: 700px;
}

.accordion_item {
  border-top: 1px solid var(--clr-primary);
}

.button {
  font-size: 16px;
  text-align: left;
  font-weight: 700;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 18px 8px;
  cursor: pointer;
  border: none;
}

.control {
  font-size: 20px;
}

.answer {
  background-color: #f7f7f7;
  padding: 20px;
  text-align: left;
}

/* activate toggle */
.accordion_item.active .button {
  background-color: var(--clr-svetro-green);
}
.answer_wrapper {
  height: 0;
  overflow: hidden;
  transition: height ease 0.2s;
}

/* amazon cards */
.serviceSection__cards {
  display: grid;
  gap: 1.5rem;
  margin-bottom: 2rem;
  justify-content: center;
}

@media (min-width: 50em) {
  .serviceSection__cards {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 80em) {
  .serviceSection__cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

#ses_badge .vertical-center a {
